import { mapActions } from 'vuex'

export default {
  props: {
    title: {
      default: '未知',
      type: String
    }
  },
  data: function () {
    return {
    }
  },
  methods: {
    headerDetailNavBarBackClick: function () {
      this.$router.go(-1)
    },
    ...mapActions('storeIndexPage', [
      'changeAsideShowFlag'
    ])
  }
}



