
import XImg from 'vux/src/components/x-img/index.vue'

import { mapActions } from 'vuex'
import imgSrc from '../../assets/logo.png'

export default {
props: {
category: {
default: ''
},
tag: {
default: '未知',
type: String
},
title: {
default: '未知',
type: String
},
author: {
default: '未知',
type: String
},
imgSrc: {
default: imgSrc,
type: String
},
forward: {
default: '未知',
type: String
},
timeBefore: {
default: '未知',
type: String
},
likeCount: {
default: 0,
type: Number
}
},
data: function () {
return {
}
},
components: {
XImg
},
methods: {
success: function () {
console.log('img lazy-load success')
},
error: function () {
console.log('img lazy-load error')
},
...mapActions('storeIndexPage', [
'changeAsideShowFlag'
])
}
}



