
import { mapGetters } from 'vuex'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import asideUserInfo from '../components/aside/asideUserInfo.vue'
import homePage from './children/homePage.vue'
import moviePage from './children/moviePage.vue'
import musicPage from './children/musicPage.vue'
import readPage from './children/readPage.vue'

export default {
name: 'indexPage',
components: {
ViewBox,
Tabbar,
TabbarItem,
'aside-user-info': asideUserInfo,
'page-home': homePage,
'page-movie': moviePage,
'page-music': musicPage,
'page-read': readPage
},
data: function () {
return {
currentViewArray: [
'page-home',
'page-read',
'page-music',
'page-movie'
],
footerTabbarIndex: 0
}
},
computed: {
...mapGetters('storeIndexPage', {
asideShowFlag: 'getAsideShowFlag'
})
},
methods: {
footerTabbarClick (index) {
this.footerTabbarIndex = index
}
}
}



